import Vue from "vue";
import Vuex from 'vuex'
// vuex

Vue.use(Vuex)

// vuex
export default new Vuex.Store({
  //
  state: {
    // input v-model
    singleJsonInputCleanState: false,
    prepareState: false
  },

  // this.$store.commit('funcName')
  mutations: {
    resetSingleJsonInputCleanState(state) {
      state.singleJsonInputCleanState = !state.singleJsonInputCleanState
    },

    resetPrepare(state) {
      state.prepareState = !state.prepareState
    }
  },

  //  this.$store.dispatch('funcName')
  actions: {

  },

  // Vuex 
  getters: {

  },

  // 
  modules: {

  },
})