<template>
  <div>
    <el-row ref="tableEdit">
      <el-col :span="2">
        <el-tooltip effect="dark" content="" placement="top-end">
          <el-button
            type="primary"
            size="small"
            plain
            @click="addRow">
            <i class="el-icon-plus"></i> 
          </el-button>
        </el-tooltip>

      </el-col>

      <el-col :span="2">
        <el-tooltip effect="dark" content="" placement="top-end">
          <el-button
            type="primary"
            size="small"
            plain
            @click="preview">
            <i class="el-icon-document"></i> 
          </el-button>
        </el-tooltip>

      </el-col>

      <el-col :span="4">
        <el-tooltip effect="dark" content="" placement="top-end">
          <el-button
            type="primary"
            size="small"
            plain
            @click="switchShow">
           <i class="el-icon-edit"></i> {{editModel}}
          </el-button>
        </el-tooltip>

      </el-col>

      <el-col :span="5">
        <el-tooltip effect="dark" content="" placement="top-end">
          <el-switch v-model="isExtend" active-text="" inactive-text="" style="margin-top: 3px">
          </el-switch>
        </el-tooltip>

      </el-col>
    </el-row>

    <el-row>
      <el-col :span="24" >
        <div ref="tablePage">
          <el-table
              v-if="showComponent === 1"
              :data="tableData"
              style="width: 100%"
              :height="tableHeight+'px'">
            <el-table-column label="" min-width="120px">
              <editable-cell slot-scope="{row}"
                             :can-edit="editModeEnabled"
                             v-model="row.name">
                <el-input
                    slot="content"
                    v-model="row.name"
                    placeholder="">
                </el-input>
              </editable-cell>
            </el-table-column>

            <el-table-column label="" min-width="120px">
              <editable-cell slot-scope="{row}"
                             :can-edit="editModeEnabled"
                             v-model="row.value">
                <el-input
                    slot="content"
                    v-model="row.value"
                    placeholder="">
                </el-input>
              </editable-cell>
            </el-table-column>

            <el-table-column label="" min-width="120px" align="center">
              <template slot-scope="scope">
                <el-button size="mini" type="danger" @click="deleteRow(scope.$index, scope.row)">
                  
                </el-button>
              </template>
            </el-table-column>
          </el-table>

          <el-row v-if="showComponent === 2">
            <el-row class="textarea-row">
              <el-col :span="24" style="padding: 15px 0" class="textarea-col">
                <el-input
                    type="textarea"
                    v-model="textarea"
                    :rows="textareaRows"
                    :style="{minHeight: textareaMinHeight+'px'}">

                </el-input>
              </el-col>
            </el-row>

          </el-row>
        </div>
      </el-col>
    </el-row>

    <result-dialog :text-content="result.result"
                   :from-dialog-visible="result.dialogVisible"
                   @changeDialogVisible="changeDialogVisible">
    </result-dialog>
  </div>

</template>

<script>
import EditableCell from "@/views/editor/common/table/EditableCell";
import { table2jsonStr, str2jsonFormat } from "@/assets/js/utils";
import ResultDialog from "@/views/editor/common/result/ResultDialog";

export default {
  name: "TableItem",
  components: {ResultDialog, EditableCell},
  props: {
    itemHeight: 0,
    isActive: false,
    isSubmit: false,
    resetFlag: false,
  },

  data() {
    return {
      editModeEnabled:  true,
      tableHeight: 0,
      showComponent: 1, //12
      switchedTextarea: false,
      isExtend: true, //
      textarea: '', // 
      textareaRows: 5, // 
      textareaMinHeight: 0, // 
      editModel: '', // 
      result: {
        result: '',
        dialogVisible: false
      },
      tableData: [{
       name: "",
       value: "",
       del: false
      },
      {
       name: "",
       value: "",
       del: false
      },
      {
       name: "",
       value: "",
       del: false
      },]
    }
  },

  methods: {
    addRow() {
      this.tableData.push({name: "", value: "", del: false})
    },

    deleteRow(index, row) {
      this.tableData.splice(index, 1)
    },
    resetHeight() {
      return new Promise((resolve, reject) => {
        this.tableHeight = 0
        resolve()
      })
    },
    // table
    setTableHeight() {
      this.resetHeight().then(res => {
        // let heightObj = this.$refs.tableEdit.$el.getBoundingClientRect()
        // console.log("heightObj");
        // console.log(heightObj);
        let tableEditBtnClientHeight = this.$refs.tableEdit.$el.clientHeight
        this.tableHeight = this.itemHeight - tableEditBtnClientHeight
        console.log('tableHeight');
        console.log(this.tableHeight);
      })
    },

    setTextareaHeight() {
      let tableEditBtnClientHeight = this.$refs.tableEdit.$el.clientHeight
      console.log('tableEditBtnClientHeight');
      console.log(tableEditBtnClientHeight);

      this.textareaMinHeight = this.itemHeight - tableEditBtnClientHeight - 30
      console.log(this.textareaMinHeight);
      this.textareaRows = this.tableHeight / 30
    },

    createResult() {
      return new Promise((resolve, reject) => {
        let tableText = table2jsonStr(this.tableData)
        let textareaContent = str2jsonFormat(this.textarea) // 
        if (this.isExtend) { // 
          // 
          if (tableText && textareaContent) { //
            this.result.result = tableText.slice(0, -1) + "," + textareaContent.slice(1, -1) + "}"
          } else if (!tableText && !textareaContent){ //"{}"
            this.result.result = "{}"
          } else if (tableText){ // 
            this.result.result = tableText || "{}"
          } else {
            this.result.result = textareaContent || "{}"
          }
        } else { //
          if (this.showComponent === 1) { //
            this.result.result = tableText || "{}"
          } else { // 
            this.result.result = textareaContent || "{}"
          }

        }

        resolve()
      })

    },

    preview() {
      this.createResult().then(res => {
        this.result.dialogVisible = true
      })
    },

    changeDialogVisible(val) {
      this.result.dialogVisible = val
      if (val === false) {
        this.result.result = ''
      }
    },

    switchShow() {
      if (this.showComponent === 1) {
        new Promise(resolve => {
          this.showComponent = 2
          this.editModel = ''
          resolve()
        }).then(res => {
          if (!this.switchedTextarea) {
          this.switchedTextarea = true
          this.setTextareaHeight()
        }
        })

      } else {
        this.showComponent = 1
        this.editModel = ''
      }
    }

  },

  watch: {
    "isActive": {
      handler: function() {
        this.setTableHeight()
      }
    },
    // "tableData": {
    //   deep: true,
    //   handler: function() {
    //     // console.log("tableDataChange");
    //     this.$emit("handleTableData", this.tableData)
    //   }
    // },
    "isSubmit": {
      handler: function () {
        if (this.isSubmit === true) {
            this.createResult().then(res => {
            this.$emit("receiveTableData", this.result.result)
          })
        }
      }
    },
    "resetFlag": {
      handler: function () {
        this.tableData = [{
           name: "",
           value: "",
           del: false
          },
          {
           name: "",
           value: "",
           del: false
          },
          {
           name: "",
           value: "",
           del: false
          },]
        this.textarea = ''
      }
    }
  }

}
</script>

<style scoped>

</style>